import { OrganizationMembershipResource } from "@clerk/types"
import { Watchlist } from "@rp/common/api-types"
import { NewWatchlistCard } from "@rp/react/web/common/watchlists"
import { WatchlistCardViewShowMoreList } from "@rp/react/web/common/watchlists"
import { Skeleton, Typography } from "@rp/react/web/mui/core"
import { MyWatchlistsList } from "../../WatchlistsListView"
import { WatchlistsModeView } from "../WatchlistsCardViewToggles"

export type MyWatchlistsProps = {
  data: Array<Watchlist & { owner: OrganizationMembershipResource | undefined }> | undefined
  containerEl: HTMLDivElement | null
  onCardClick: (watchlist: Watchlist) => void
  onNewWatchlistClick: () => void
  initialLines?: number
  isLoading?: boolean
  modeView: WatchlistsModeView
}

export const MyWatchlists = ({
  containerEl,
  onCardClick,
  data,
  onNewWatchlistClick,
  initialLines,
  isLoading,
  modeView,
}: MyWatchlistsProps) => {
  if (data && data?.length === 0) {
    return (
      <>
        <Typography sx={{ fontSize: 32, fontWeight: 700 }}>Your Watchlists</Typography>
        <Typography sx={{ color: "text.secondary", fontSize: 16, my: 2 }}>
          You haven't created any watchlist yet
        </Typography>
        <NewWatchlistCard width="40%" backgroundColor="transparent" onClick={onNewWatchlistClick} />
      </>
    )
  }

  return (
    <>
      <Typography sx={{ fontSize: 32, fontWeight: 700 }}>
        {isLoading ? <Skeleton width={200} /> : "Your Watchlists"}
      </Typography>
      <Typography sx={{ color: "text.secondary", fontSize: 16, my: 2 }}>
        {isLoading ? <Skeleton width={400} /> : "These are the watchlists that you have created"}
      </Typography>
      {modeView === "card" && (
        <WatchlistCardViewShowMoreList
          onCardClick={onCardClick}
          containerEl={containerEl}
          data={data}
          lastElement={<NewWatchlistCard onClick={onNewWatchlistClick} />}
          initialLines={initialLines}
        />
      )}
      {modeView === "list" && <MyWatchlistsList isLoading={isLoading} data={data} />}
    </>
  )
}
